/* eslint-disable jsx-a11y/alt-text */
// App.js
import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Scrollbar, Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/scrollbar";
import "swiper/css/navigation";
import "./style.css";
import Card from "./Card"; // Card 컴포넌트 import
import data from "./data"; // 데이터 import

export default function ProductAll() {
  // 향수와 캔들 데이터를 각각 필터링
  const perfumeData = data.filter((item) => item.list === "f");
  const candleData = data.filter((item) => item.list === "c");

  return (
    <>
      <Swiper
        scrollbar={{ hide: false }}
        modules={[Autoplay, Scrollbar, Navigation]} // 필요한 모듈만 추가
        spaceBetween={30} // 슬라이드 간 간격
        centeredSlides={true} // 중앙 정렬
        loop={true} // 슬라이드 끝나면 처음으로 돌아가기
        autoplay={{
          delay: 2500, // 2.5초마다 슬라이드 전환
          disableOnInteraction: false, // 사용자가 클릭해도 자동 재시작
        }}
        pagination={{ clickable: true }} // 페이지네이션 클릭 가능하게 설정
        navigation={true} // 네비게이션 버튼 활성화
        className="mySwiper"
      >
        {/* 이미지 슬라이드 */}
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/001.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/002.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/003.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/004.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/005.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/006.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/007.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/008.jpg`} />
        </SwiperSlide>
        <SwiperSlide>
          <img src={`${process.env.PUBLIC_URL}/img/009.jpg`} />
        </SwiperSlide>
      </Swiper>

      {/* 향수 섹션 */}
      <h2>Perfume</h2>
      <div className="card-container">
        {perfumeData.map((item) => (
          <Card
            key={item.id}
            id={item.id}
            img={item.img}
            title={item.title}
            content={item.content}
            price={item.price}
            list={item.list} // list 추가
            category="f" // 향수 카테고리
          />
        ))}
      </div>
      <img src={`${process.env.PUBLIC_URL}/img/banner.png`} alt="banner" />

      {/* 캔들 섹션 */}
      <h2>Candle</h2>
      <div className="card-container">
        {candleData.map((item) => (
          <Card
            key={item.id}
            id={item.id}
            img={item.img}
            title={item.title}
            content={item.content}
            price={item.price}
            list={item.list} // list 추가
            category="c" // 캔들 카테고리
          />
        ))}
      </div>
    </>
  );
}
