// ProductDetail.js
import React from "react";
import data from "./data"; // 데이터 파일에서 모든 데이터를 가져옵니다.
import { useParams } from "react-router-dom";
import "./ProductDetail.css";

function ProductDetail() {
  const { category, id } = useParams(); // URL에서 'category'와 'id'를 가져옴
  // category와 id에 맞는 상품 데이터 찾기
  const product = data.find(
    (item) => item.id === parseInt(id) && item.list === category // id는 문자열이므로 parseInt로 숫자로 변환
  );

  if (!product) {
    return <div>Product not found</div>; // 상품이 없으면 에러 메시지 출력
  }

  return (
    <div className="product-detail">
      <div className="wrap">
        <div className="product-image">
          <img src={product.img} alt={product.title} />
        </div>
        <div className="product-info">
          <h2>{product.title}</h2>
          <p className="price">{product.price} 원</p>
          <p className="content">{product.content}</p>

          <div className="shipping-info">
            <h4>배송 정보</h4>
            <p>배송비: 무료</p>
          </div>

          <div className="product-options">
            <h4>옵션 선택</h4>
            <select>
              <option>옵션 1</option>
              <option>옵션 2</option>
              <option>옵션 3</option>
            </select>
          </div>

          <div className="buy-now">
            <button className="btn-buy">지금 구매</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductDetail;
